import { Component, Prop, h, Element, State, Event, EventEmitter } from '@stencil/core';

export type LabelLayout = 'block' | 'inline' | 'inline-start' | 'inline-space-between';

@Component({
  tag: 'bh-label',
  styleUrl: 'bh-label.css',
  shadow: true,
})
export class BhLabel {
  @Element() el!: HTMLElement;

  private tooltipTimerRef?: ReturnType<typeof setTimeout>;

  /**
   * The label text to display
   */
  @Prop() label: string = '';

  /**
   * Layout style for the label container
   * - block: Label above content (default for form fields)
   * - inline: Label next to content (label first)
   * - inline-start: Content next to label (content first, for checkboxes/radios)
   * - inline-space-between: Label and content on opposite ends
   */
  @Prop() layout: LabelLayout = 'block';

  /**
   * The id of the form element the label is bound to.
   * Use when the labeled element is not a child of this component.
   */
  @Prop() for: string = '';

  /**
   * Supporting text displayed below the label (second line)
   * for instructions or additional description
   */
  @Prop() supportingText: string = '';

  /**
   * Whether to show the required asterisk
   */
  @Prop() required: boolean = false;

  /**
   * Whether to show the help icon
   */
  @Prop() showHelpIcon: boolean = false;

  /**
   * Tooltip text for the help icon
   */
  @Prop() helpIconTooltip: string = 'Help';

  /**
   * Whether the label is disabled
   */
  @Prop() disabled: boolean = false;

  @State() showHelpTooltip: boolean = false;

  /**
   * Emitted when the help icon is clicked
   */
  @Event() bhHelpClick!: EventEmitter<void>;

  disconnectedCallback() {
    if (this.tooltipTimerRef) {
      clearTimeout(this.tooltipTimerRef);
    }
  }

  private handleHelpIconMouseEnter = () => {
    if (this.tooltipTimerRef) {
      clearTimeout(this.tooltipTimerRef);
    }
    this.tooltipTimerRef = setTimeout(() => {
      this.showHelpTooltip = true;
    }, 300);
  };

  private handleHelpIconMouseLeave = () => {
    if (this.tooltipTimerRef) {
      clearTimeout(this.tooltipTimerRef);
      this.tooltipTimerRef = undefined;
    }
    this.showHelpTooltip = false;
  };

  private handleHelpIconClick = () => {
    this.bhHelpClick.emit();
  };

  render() {
    const wrapperClasses = {
      'label-container': true,
      [`layout-${this.layout}`]: true,
      'disabled': this.disabled,
    };

    const labelClasses = {
      'label-text': true,
      'disabled': this.disabled,
    };

    const supportingClasses = {
      'supporting-text': true,
      'disabled': this.disabled,
    };

    const labelWrapper = (
      <div class="label-wrapper">
        {this.label && (
          <label class={labelClasses} htmlFor={this.for || undefined}>
            {this.label}
          </label>
        )}
        {this.required && <span class="asterisk">*</span>}
        {this.showHelpIcon && (
          <div class="icon-with-tooltip">
            <span
              class="help-icon material-symbols-outlined"
              onClick={this.handleHelpIconClick}
              onMouseEnter={this.handleHelpIconMouseEnter}
              onMouseLeave={this.handleHelpIconMouseLeave}
            >
              help
            </span>
            <bh-tooltip
              text={this.helpIconTooltip}
              position="top"
              visible={this.showHelpTooltip}
            />
          </div>
        )}
      </div>
    );

    const supportingTextEl = this.supportingText && (
      <p class={supportingClasses}>{this.supportingText}</p>
    );

    const contentSlot = (
      <div class="label-content">
        <slot></slot>
      </div>
    );

    // For inline-start layout, content comes first (used for checkbox/radio)
    if (this.layout === 'inline-start') {
      return (
        <div class={wrapperClasses}>
          {contentSlot}
          <div class="label-text-group">
            {labelWrapper}
            {supportingTextEl}
          </div>
        </div>
      );
    }

    // Default layout: label wrapper first, then content
    return (
      <div class={wrapperClasses}>
        {labelWrapper}
        {supportingTextEl}
        {contentSlot}
      </div>
    );
  }
}
